export default {
  name: 'IndexTitle',
  components: {},
  data() {
    return {
      title: '',
      subtitle: '',
      image: '',
    }
  },
  async fetch() {
    const { title, subtitle, image } = await this.$content('pages/index')
      .only(['title', 'subtitle', 'image'])
      .fetch()

    this.title = title
    this.subtitle = subtitle
    this.image = image
  },
  computed: {
    imageLink() {
      if (!this.image) {
        return '/uploads'
      }
      return '/uploads/' + this.image.replace('/static/uploads/', '')
    },
  },
}
