import { VueTube } from 'vuetube'
export default {
  name: 'Card',
  components: {
    VueTube,
  },
  props: {
    title: {
      type: String,
      required: true,
    },
    subtitle: {
      type: String,
      default: '',
      required: false,
    },
    date: {
      type: String,
      default: '',
      required: true,
    },
    image: {
      type: String,
      default: null,
      required: false,
    },
    videoId: {
      type: String,
      default: null,
      required: false,
    },
    offset: {
      type: Boolean,
      default: false,
      required: false,
    },
    vueHorizontalChild: {
      type: Boolean,
      default: false,
      required: false,
    },
    link: {
      type: String,
      default: '',
      required: false,
    },
    imageAlt: {
      type: String,
      default: '',
      required: false,
    },
  },
  computed: {
    imageLink() {
      if (!this.image) {
        return
      }
      return '/uploads/' + this.image.replace('/static/uploads/', '')
    },
  },
}
