export default {
  name: 'IndexText',
  components: {},
  data() {
    return {
      shortSentence: null,
    }
  },
  async fetch() {
    const content = await this.$content('pages/index')
      .only(['shortSentence'])
      .fetch()
    this.shortSentence = content.shortSentence
  },
}
